<template>
  <div>
    <div>
      <el-input
        v-model="jobLevel.name"
        placeholder="添加职称等级..."
        prefix-icon="el-icon-plus"
        style="width: 300px"
        size="small">
      </el-input>
      <el-select size="small" v-model="jobLevel.titleLevel" placeholde="请选择" style="margin-left: 8px">
        <el-option
          v-for="item in titleLevels"
          :key="item"
          :label="item"
          :value="item"></el-option>
      </el-select>
      <el-button size="small" type="primary" icon="el-icon-plus" class="add" @click="addJobLevel">add</el-button>
    </div>
    <div style="margin-top: 10px">
      <el-table
        :data="jobLevels"
        stripe
        border
        style="width: 70%"
        size="small"
        @selection-change="handleSelectChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="id" width="55"></el-table-column>
        <el-table-column prop="name" label="name" width="150"></el-table-column>
        <el-table-column prop="titleLevel" label="titleLevel" width="150"></el-table-column>
        <el-table-column prop="createDate" label="createDate" width="150"></el-table-column>
        <el-table-column prop="enabled" label="enabled" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.enabled" type="success">enable</el-tag>
            <el-tag v-else type="danger">disable</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="action">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditView(scope.row)">edit</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row)">delete</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-button
        type="danger"
        style="margin-top: 8px"
        size="small"
        :disabled="this.multipleSelection.length==0"
        @click="deleteMany">批量删除
      </el-button>
    </div>
    <el-dialog title="编辑职称" :visible.sync="dialogVisible" width="30%">
      <table>
        <tr>
          <td>
            <el-tag>职称名称</el-tag>
          </td>
          <td>
            <el-input class="updateNameInput" v-model="updateJob.name" size="small" style="margin-left: 6px"></el-input>
          </td>
        </tr>
        <tr>
          <td>
            <el-tag>职称等级</el-tag>
          </td>
          <td>
            <el-select size="small" v-model="updateJob.titleLevel" placeholde="请选择" style=" margin-left: 6px">
              <el-option
                v-for="item in titleLevels"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </td>
        </tr>
        <tr>
          <td>
            <el-tag>是否启用</el-tag>
          </td>
          <td>
            <el-switch
              style="margin-left: 6px"
              v-model="updateJob.enabled"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="enable"
              inactive-text="disable"></el-switch>
          </td>
        </tr>
      </table>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">cancel</el-button>
        <el-button size="small" type="primary" @click=doUpdate>confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'JobLevel',
  data () {
    return {
      jobLevel: {
        name: '',
        titleLevel: '',
        enabled: false
      },
      titleLevels: [
        '正高级',
        '副高级',
        '中级',
        '初级',
        '员级'
      ],
      jobLevels: [],
      updateJob: {
        name: '',
        titleLevel: '',
        enabled: false
      },
      dialogVisible: false,
      multipleSelection: []
    }
  },
  mounted () {
    this.initJobLevels()
  },
  methods: {
    initJobLevels () {
      this.getRequest('/system/basic/joblevel/').then(resp => {
        if (resp) {
          this.jobLevels = resp
          this.jobLevel.name = ''
          this.jobLevel.titleLevel = ''
        }
      })
    },
    addJobLevel () {
      if (this.jobLevel.name && this.jobLevel.titleLevel) {
        this.postRequest('/system/basic/joblevel/', this.jobLevel).then(resp => {
          if (resp) {
            this.initJobLevels()
          }
        })
      } else {
        this.$message.error('字段不能为空')
      }
    },
    showEditView (data) {
      Object.assign(this.updateJob, data)
      this.updateJob.createDate = ''
      this.dialogVisible = true
    },
    doUpdate () {
      this.putRequest('/system/basic/joblevel/', this.updateJob).then(resp => {
        if (resp) {
          this.initJobLevels()
          this.dialogVisible = false
        }
      })
    },
    handleDelete (data) {
      this.$confirm('此操作将删除[' + data.name + ']的所有信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/system/basic/joblevel/' + data.id).then(resp => {
          console.log(resp)
          if (resp) {
            this.initJobLevels()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleSelectChange (val) {
      this.multipleSelection = val
    },
    deleteMany () {
      this.$confirm('此操作将删除' + this.multipleSelection.length + '条职位信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids = '?'
        this.multipleSelection.forEach(item => {
          ids += 'ids=' + item.id + '&'
        })
        this.deleteRequest('/system/basic/joblevel/' + ids).then(resp => {
          if (resp) {
            this.initJobLevels()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style scoped>
.add {
  border-radius: 5px;
  margin-left: 5px;
}
</style>
