<template>
  <div>
    <div style="display: flex; justify-content: center; margin-top: 10px">
      <el-input
        placeholder="通过用户名搜索用户..."
        prefix-icon="el-icon-search"
        style="width: 400px; margin-right: 8px"
        v-model="keywords"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="doSearch">search</el-button>
    </div>
    <div class="admin_container">
      <el-card v-for="(admin, index) in admins" :key="index" class="admin_card">
        <div slot="header">
          <span>{{ admin.name }}</span>
          <el-button
            style="float: right; padding: 3px 0; color: red"
            type="text"
            icon="el-icon-delete"
            @click="deleteAdmin(admin)"></el-button>
        </div>
        <div>
          <div class="userInfo_container">
            <div>name: {{ admin.name }}</div>
            <div>phone: {{ admin.phone }}</div>
            <div>telephone: {{ admin.telephone }}</div>
            <div>address: {{ admin.address }}</div>
            <div>
              enable:
              <el-switch
                v-model="admin.enabled"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="enabled"
                inactive-text="disabled"
                @change = "enableUpdate(admin)">
              </el-switch>
            </div>
            <div>
              role:
              <el-tag
                style="margin-right: 4px"
                type="success"
                v-for="(role, index2) in admin.roles"
                :key="index2">{{role.nameZh}}
              </el-tag>
              <el-popover
                placement="right"
                title="角色列表"
                width="200"
                trigger="click"
                @show="showPop(admin)"
                @hide="hidePop(admin)">
                <el-select v-model="selectRoles" multiple placeholder="请选择">
                  <el-option v-for="(r, index) in allRoles" :key="index" :label="r.nameZh" :value="r.id"></el-option>
                </el-select>
                <el-button type="text" icon="el-icon-more" slot="reference"></el-button>
              </el-popover>
            </div>
            <div>remark: {{ admin.remark }}</div>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SysAdmin',
  data () {
    return {
      admins: [],
      keywords: '',
      allRoles: [],
      selectRoles: []
    }
  },
  mounted () {
    this.initAdmins()
  },
  methods: {
    initAdmins () {
      this.getRequest('/system/admin/').then(resp => {
        if (resp) {
          this.admins = resp
        }
      })
    },
    doSearch () {
      this.getRequest('/system/admin/?keywords=' + this.keywords).then(resp => {
        if (resp) {
          this.admins = resp
        }
      })
    },
    deleteAdmin (admin) {
      this.$confirm('此操作将删除操作员[' + admin.name + ']的信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/system/admin/' + admin.id).then(resp => {
          if (resp) {
            this.initAdmins()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    enableUpdate (admin) {
      this.putRequest('/system/admin/', {
          id: admin.id,
          enabled: admin.enabled
        }
      ).then(resp => {
        if (resp) {
          this.initAdmins()
        }
      })
    },
    initAllRoles () {
      this.getRequest('/system/admin/roles').then(resp => {
        if (resp) {
          this.allRoles = resp
        }
      })
    },
    showPop (admin) {
      this.initAllRoles()
      let roles = admin.roles
      this.selectRoles = []
      roles.forEach(r => {
        this.selectRoles.push(r.id)
      })
    },
    hidePop (admin) {
      this.putRequest('/system/admin/role', {
        adminId: admin.id,
        rids: this.selectRoles
      }).then(resp => {
        if (resp) {
          this.initAdmins()
        }
      })
    }
  }
}
</script>

<style scoped>
.admin_container {
  display: flex;
  margin-top: 10px;
  justify-content: space-around;
  flex-wrap: wrap;
}

.admin_card {
  width: 350px;
  margin-bottom: 20px;
}

.userInfo_container {
  font-size: 12px;
  color: #409eff;
  margin-bottom: 10px;
}
</style>
