<template>
  <div style="width: 500px">
    <el-input placeholder="请输入部门名称进行搜索..." v-model="filterText" prefix-icon="el-icon-search"></el-input>
    <el-tree
      :data="departments"
      :props="defaultProps"
      :filter-node-method="filterNode"
      :expand-on-click-node="false"
      ref="tree">
      <span slot-scope="{ node, data }" style="display: flex; justify-content: space-between; width: 100%;">
        <span> {{ data.name }}</span>
        <span>
          <el-button type="primary" size="mini" @click="() => showAddDep(data)" class="depBtn">add</el-button>
          <el-button type="danger" size="mini" @click="() => deleteDep(data)" class="depBtn">delete</el-button>
        </span>
      </span>
    </el-tree>
    <el-dialog title="添加部门" :visible.sync="dialogVisible" width="30%">
      <div>
        <table>
          <tr>
            <td>
              <el-tag>上级部门</el-tag>
            </td>
            <td>{{ pName }}</td>
          </tr>
          <tr>
            <td>
              <el-tag>部门名称</el-tag>
            </td>
            <td>
              <el-input v-model="dep.name" placeholder="请输入部门名称"></el-input>
            </td>
          </tr>
        </table>
      </div>
      <span slot="footer">
        <el-button @click="dialogVisible = false">cancel</el-button>
        <el-button type="primary" @click="addDep">confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'DepMana',
  data () {
    return {
      filterText: '',
      departments: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      dialogVisible: false,
      dep: {
        name: '',
        parentId: -1
      },
      pName: ''
    }
  },
  mounted () {
    this.initDepartments()
  },
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val)
    }
  },
  methods: {
    initDepartments () {
      this.getRequest('/system/basic/department/').then(resp => {
        if (resp) {
          this.departments = resp
        }
      })
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    showAddDep (data) {
      this.pName = data.name
      this.dep.parentId = data.id
      this.dialogVisible = true
    },
    addDep () {
      this.postRequest('/system/basic/department/', this.dep).then(resp => {
        if (resp) {
          this.addDepToDeps(this.departments, resp.object)
          this.dialogVisible = false
          this.initDep()
        }
      })
    },
    initDep () {
      this.dep = {
        name: '',
        parentId: -1
      }
      this.pName = ''
    },
    addDepToDeps (deps, dep) {
      for (let i = 0; i < deps.length; i++) {
        let d = deps[i]
        if (d.id === dep.parentId) {
          d.children = d.children.concat(dep)
          if (d.children.length > 0) {
            d.isParent = true
          }
          return
        } else {
          this.addDepToDeps(d.children, dep)
        }
      }
    },
    deleteDep (data) {
      if (data.isParent) {
        this.$message.error('父部门删除失败')
      } else {
        this.$confirm('此操作将删除[' + data.name + ']的角色信息，是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteRequest('/system/basic/department/' + data.id).then(resp => {
            if (resp) {
              this.removeDepFromDeps(null, this.departments, data.id)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    removeDepFromDeps (p, deps, id) {
      for (let i = 0; i < deps.length; i++) {
        let d = deps[i]
        if (d.id === id) {
          deps.splice(i, 1)
          if (deps.length === 0) {
            p.isParent = false
          }
          return
        } else {
          this.removeDepFromDeps(d, d.children, id)
        }
      }
    }
  }
}
</script>

<style scoped>
.depBtn {
  padding: 2px;
}
</style>
