<template>
  <div>
    <div>
      <el-input
        placeholder="添加职位..."
        suffix-icon="el-icon-plus"
        v-model="pos.name"
        class="addPosInput"
        size="small"
        @keydown.enter.native="addPosition">
      </el-input>
      <el-button icon="el-icon-plus" size="small" class="add" @click="addPosition" type="primary">add</el-button>
    </div>
    <div class="posMana">
      <el-table
        :data="positions"
        style="width: 70%"
        stripe
        border
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="id" width="55"></el-table-column>
        <el-table-column prop="name" label="name" width="120"></el-table-column>
        <el-table-column prop="createDate" label="createDate" width="200"></el-table-column>
        <el-table-column label="actions ">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditView(scope.$index, scope.row)">edit</el-button>
            <el-button size="small" @click="handleDelete(scope.$index, scope.row)" type="danger">delete</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-button
      type="danger"
      style="margin-top: 8px"
      size="small"
      :disabled="this.multipleSelection.length==0"
      @click="deleteMany">批量删除</el-button>
    <el-dialog
      title="编辑职位"
      :visible.sync="dialogVisible"
      width="30%">
      <div>
        <el-tag>职位名称</el-tag>
        <el-input class="updatePosInput" v-model="updatePos.name"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">cancel</el-button>
        <el-button size="small" type="primary" @click=doUpdate>confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'PosMana',
  data () {
    return {
      pos: {
        name: ''
      },
      positions: [],
      dialogVisible: false,
      updatePos: {
        name: ''
      },
      multipleSelection: []
    }
  },
  mounted () {
    this.initPositions()
  },
  methods: {
    initPositions () {
      this.getRequest('/system/basic/position/').then(resp => {
        if (resp) {
          this.positions = resp
          this.pos.name = ''
        }
      })
    },
    showEditView (index, data) {
      Object.assign(this.updatePos, data)
      this.updatePos.createDate = ''
      this.dialogVisible = true
    },
    handleDelete (index, data) {
      this.$confirm('此操作将删除[' + data.name + ']的职位信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/system/basic/position/' + data.id).then(resp => {
          if (resp) {
            this.initPositions()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    addPosition () {
      if (this.pos.name) {
        this.postRequest('/system/basic/position/', this.pos).then(resp => {
          if (resp) {
            this.initPositions()
          }
        })
      } else {
        this.$message.error('职位名称不能为空 ')
      }
    },
    doUpdate () {
      this.putRequest('/system/basic/position/', this.updatePos).then(resp => {
        if (resp) {
          this.initPositions()
          this.dialogVisible = false
        }
      })
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    deleteMany () {
      this.$confirm('此操作将删除' + this.multipleSelection.length + '条职位信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids = '?'
        this.multipleSelection.forEach(item => {
          ids += 'ids=' + item.id + '&'
        })
        this.deleteRequest('/system/basic/position/' + ids).then(resp => {
          if (resp) {
            this.initPositions()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style scoped>
.addPosInput {
  width: 300px;
  background-color: #f2ebd1;
}

.add {
  border-radius: 5px;
  margin-left: 5px;
}

.posMana {
  margin-top: 10px;
}

.updatePosInput {
  width: 200px;
  margin-left: 8px;
}
</style>
