<template>
  <div>
    <div class="PermissManaTool">
      <el-input placeholder="请输入角色英文名" size="small" v-model="role.name">
        <template slot="prepend">ROLE_</template>
      </el-input>
      <el-input placeholder="请输入角色中文名" size="small" v-model="role.nameZh" @keydown.enter="addRole"></el-input>
      <el-button type="primary" size="small" icon="el-icon-plus" @click="addRole">add</el-button>
    </div>
    <div class="PermissManaMain">
      <el-collapse v-model="activeName" accordion @change="change">
        <el-collapse-item :title="r.nameZh" :name="r.id" v-for="(r, index) in roles" :key="index">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>可访问资源</span>
              <el-button
                style="float: right; padding: 3px; color: red"
                type="text"
                icon="el-icon-delete"
                @click="deleteRole(r)"></el-button>
            </div>
            <div>
              <el-tree
                :data="allMenus"
                :props="defaultProps"
                ref="tree"
                show-checkbox
                :key="index"
                :default-checked-keys="selectedMenus"
                node-key="id"></el-tree>
              <div style="display: flex; justify-content: flex-end">
                <el-button size="mini" @click="cancelUpdate">cancel</el-button>
                <el-button size="mini" type="primary" @click="doUpdate(r.id, index)">confirm</el-button>
              </div>
            </div>
          </el-card>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PermissMana',
  data () {
    return {
      role: {
        name: '',
        nameZh: ''
      },
      roles: [],
      allMenus: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      selectedMenus: [],
      activeName: -1
    }
  },
  mounted () {
    this.initRoles()
  },
  methods: {
    initRoles () {
      this.getRequest('/system/basic/permission/').then(resp => {
        if (resp) {
          this.roles = resp
        }
      })
    },
    change (rid) {
      if (rid) {
        this.initAllMenus()
        this.initSelectedMenus(rid)
      }
    },
    initAllMenus () {
      this.getRequest('/system/basic/permission/menus').then(resp => {
        if (resp) {
          this.allMenus = resp
        }
      })
    },
    initSelectedMenus (rid) {
      this.getRequest('/system/basic/permission/mid/' + rid).then(resp => {
        if (resp) {
          this.selectedMenus = resp
        }
      })
    },
    // you wen ti
    doUpdate (rid, index) {
      let tree = this.$refs.tree[index]
      let selectedKeys = tree.getCheckedKeys(true)
      this.putRequest('/system/basic/permission/', {
        rid: rid,
        mIds: selectedKeys
      }).then(resp => {
        if (resp) {
          this.activeName = -1
        }
      })
    },
    cancelUpdate () {
      this.activeName = -1
    },
    addRole () {
      if (this.role.name && this.role.nameZh) {
        this.postRequest('/system/basic/permission/role', this.role).then(resp => {
          if (resp) {
            this.initRoles()
            this.role.name = ''
            this.role.nameZh = ''
          }
        })
      } else {
        this.$message.error('所有字段不能为空')
      }
    },
    deleteRole (role) {
      this.$confirm('此操作将删除[' + role.name + ']的角色信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/system/basic/permission/role/' + role.id).then(resp => {
          if (resp) {
            this.initRoles()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style scoped>
.PermissManaTool {
  display: flex;
  justify-content: flex-start;
}

.PermissManaMain {
  margin-top: 10px;
  width: 700px;
}
</style>
